body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #333;
  overflow-x: hidden;
  overflow-y: scroll;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.git-logo{
  font-size: 2rem;
}
.router{
  font-size: 1.5rem;
  text-decoration: none;
  font-style: none;
  font-weight: bold;
}
.navbar{
  box-shadow: 15px 0px 20px rgba(0, 0, 0, 1 );
  position: fixed;
  width: 100%;
  z-index: 10;
}
.footer{
  padding: 10px 0;
  position: fixed;
  width: 100%;
}

.owner{
 font-weight: bold;

}
.main{
  height: 93vh;
  padding: 0 2rem;
}
.not-found{
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  flex-direction: column;
}
.ops{
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  flex-direction: column;
}
.itemlist{
  height: 50vh;
}
.user{
  padding: 1rem;
  margin: 5px;
  border: 1px solid rgba(255, 255, 255, .5);
  background-color:rgba(255, 255, 255, .1);
  border-radius: 5px;
}
.avatar{
  width:50px;
  border-radius:50%;
}
.card{
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 1 );
  display: flex ;
  padding: 0;
}

.profile{
  text-decoration: none;
  font-size: 15px;
}
.row{
  display: flex;
  align-items: center;
  justify-content: center;
  padding-bottom: 5rem;
}
.info{
  display: flex;
  flex-direction: column;
  align-items:center;
  justify-content: center;
}
.img{
  display: flex;
  width: fit-content;
  flex-direction: column;
  z-index: 10;
  position: relative;
  text-align: center;
}
.imgwidth{
  width: fit-content;
  width: 100%;

}
 .info{
   display: flex;
   align-items:center;
   justify-content: center;
 }

.right{
  margin-left: auto;
}
.left{
  display: flex;
  align-items:center;
  justify-content: center;
  flex-direction: column;
}
.icon{
  font-size: 2rem;
  color: purple;
}
.col{
  padding: 5px 10px;
  border-right: .5px solid rgba(255, 255, 255, .1);
}
.span{
  font-size: 13px;
  opacity: .8;
}
.flex{
  flex-wrap:wrap ;
}

.containing{
  width: 100%;
  padding:5rem 2rem;margin-top: 6rem;
}
.about{
  padding: 4rem 5rem;
}

@media (max-width:556px){
  .imgwidth{
    border-radius:50%;
  }
}
